import {defineStore} from "pinia/dist/pinia";


export const storeProactive = defineStore('storeProactive',{
    state:(()=>{
        return {
            metrics:[],
            unitsTable:[],
            status:[],
        }
    }),
    actions:{
        async loadStatusProactive(){
            const stat = await  new Promise(resolve => {
                BX.rest.callMethod('mwsproactiverest.abonentStatus.getList',{select:['*']},res=>{
                   resolve(res.data())

                } )
            })
            this.status = stat

        },
        async loadMainTable() {
            const mainTable = await new Promise((resolve => {
                BX.rest.callMethod("mwsproactive.ui.proactiveTableUi", {}, res => {resolve(res.data())})
            }))

            this.metrics = mainTable
        },
        async loadUnits(abonentId,metric){
            this.unitsTable = []
            const units =  await new Promise((resolve)=>{
                BX.rest.callMethod('mwsproactiverest.metricunit.getList',{filter:{
                        ABONENT_ID:abonentId,
                        METRIC_ASUO:metric
                        },
                    select:['*']
                },res => {
                    resolve(res.data())
                })
            })
            this.unitsTable = units
        },
        async createDeal(){

        }

    },
    getters:{
        getMetrics(state){
            return state.metrics
        },
        getUnitsTable(state){
            return state.unitsTable;
        },
        getStatus(state){
            return state.status
        }
    }
})